import './utils/extend'
const importAll = (r) => {
    return r.keys().map(r)
}

let components = importAll(require.context('./components', false, /\.vue$/))


export default {
    install(Vue, options) {
            components.forEach(component => {
                let name = component.default.name
                Vue.component(name, component.default)
            })                
    }
}

