<script>
    /**
     * open,close map
     */
    import globalmixin from '@/mixins/globalmixin'
    import childmixin from '@/mixins/childmixin'
    import {translateVnode} from '@/utils/util'
    import {popup} from 'leaflet'

    const TAG_NAME = "hb-map-popup"

    export default {
        name: TAG_NAME,
        mixins: [
            globalmixin,
            childmixin,
        ],
        data() {
            return {
                tagName: TAG_NAME
            }
        },
        props: {
            visible: {
                type: Boolean,
                default: true
            },
        },
        beforeDestroy() {
            this.__map.removeFrom(this.getMap());
            this.getMap().off('popupopen', this.handlePopupOpen);
            this.getMap().off('popupclose', this.handlePopupClose);
        },
        watch: {
            visible(val) {
                // console.log(val);
                if (!val) {
                    this.getMap().closePopup();
                } else {
                    // console.log(this.getMap());
                    this.$nextTick(() => {
                        this.__map.setContent(this.$el)
                                  .openOn(this.getMap());
                    })
                }
                // console.log(val);
                // val ? this.getMap().openPopup() : this.getMap().closePopup();
                // if (val) {
                //     this.__map.openPopup();
                //     // this.__map.remove();
                // } else {
                //     this.__map.closePopup();
                //     // this.createPopup();
                // }
            },
            position(latlng) {
                // console.log(latlng);
                latlng && this.__map.setLatLng(latlng) && this.__map.update();
                // latlng && this.getMap().openPopup(latlng);
                // latlng && this.__map.openPopup(latlng);
            }
        },
        methods: {
            createPopup() {
                // console.log(1);
                this.__map = popup(this.options);
                this.__map.setLatLng(this.position);
                this.$el && this.__map.setContent(this.$el);
                // this.__map.openOn(this.getMap());
                this.getMap().on('popupopen', this.handlePopupOpen);
                this.getMap().on('popupclose', this.handlePopupClose);
            },
            handlePopupOpen() {
                this.$emit('open');
            },
            handlePopupClose() {
                this.$emit('close');
            }
        },
        render(h) {
            if (this.visible) {
                return h('div', this.$slots.default)
            }
            return null
        }
    }
</script>
